import { Injectable } from '@angular/core';
import { Http, Response, Headers } from '@angular/http';

import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';

import { User } from '../../models/user';

import { environment } from '../../../../environments/environment';

@Injectable()
export class UserService {
  
  private usersUrl: string = environment.url + "users";
  
  loggedIn;

  // Observable source
  private userCreatedSource  = new Subject<User>();
  private userDeletedSource  = new Subject();  

  // Observable Stream
  userCreated$  = this.userCreatedSource.asObservable();
  userDeleted$  = this.userDeletedSource.asObservable();

  constructor(private http: Http) { }

  /**
   * Get All Users
   */
  // getUsers(): Observable<User[]> {
  //   // Attaching a token
  //   let headers = new Headers();
  //   let token = localStorage.getItem('auth_token');
  //   headers.append('Content-Type', 'application/json');
  //   headers.append('Access-Token', `Bearer ${token}`);

  //   return this.http.get(this.usersUrl, { headers })
  //     .map(res => res.json().data.users) // Remember map here is an Observable
  //     .catch(this.handleError);
  // }

  getUsers(page: number): Observable<User[]> {
    let headers = new Headers();
    let token = localStorage.getItem('auth_token');
    headers.append('Content-Type', 'application/json');
    headers.append('Access-Token', `Bearer ${token}`);
    
    const offset = (page - 1) * 10;
    return this.http.get(this.usersUrl + '?limit=10' + '&offset=' + offset, { headers })
      .map( res => res.json() )
      .catch( this.handleError );
  }

  /**
   * Get A Single User
   */
  getUser(id: number): Observable<User> {
    // Attaching a token
    let headers = new Headers();
    let token = localStorage.getItem('auth_token');
    headers.append('Content-Type', 'application/json');
    headers.append('Access-Token', `Bearer ${token}`);

    return this.http.get(`${this.usersUrl}/${id}`, { headers })
      .map(res => res.json().data.users)
      .map(this.toUser) // This style is used for single object
      .catch(this.handleError);
  }
  
  /**
   * Get Users as Owners List 
   */
  getOwners() {
    // Attaching a token
    let headers = new Headers();
    let token = localStorage.getItem('auth_token');
    headers.append('Content-Type', 'application/json');
    headers.append('Access-Token', `Bearer ${token}`);

    return this.http.get(`${this.usersUrl}/owners`, { headers })
             .map(res => res.json().data.owners)
             .catch(this.handleError)
  }

 /**
  * Create A User
  */  
  createUser(user: User): Observable<User> {
    // Attaching a token
    let headers = new Headers();
    let token = localStorage.getItem('auth_token');
    headers.append('Content-Type', 'application/json');
    headers.append('Access-Token', `Bearer ${token}`);

    if (user.picture_attributes) {
      if ( (user.picture_attributes.id === '') && (user.picture_attributes.name === '') 
          || (user.picture_attributes.id === undefined) && (user.picture_attributes.name === undefined) ){
      delete user.picture_attributes;
      }
    }
    
    return this.http.post(this.usersUrl, user, {headers})
      .map(res => res.json().data === null ? res.json().message : res.json().data.users)
      .do(unit => this.userCreated(user))
      .catch(this.handleError)
  }

 /**
  * Update A User
  */ 
  updateUser(user: User): Observable<User> {
    // Attaching a token
    let headers = new Headers();
    let token = localStorage.getItem('auth_token');
    headers.append('Content-Type', 'application/json');
    headers.append('Access-Token', `Bearer ${token}`);

    // put has url and data or information as second parameter
    return this.http.put(`${this.usersUrl}/${user.id}`, user, {headers}) 
      .map(res => res.json().data.users)
      .map(this.toUser)
      .catch(this.handleError);
  }

  /**
   * Delete A User
   */
  deleteUser(id: number): Observable<any>{ // If we want this to return something that use Observable<any>
    // Attaching a token
    let headers = new Headers();
    let token = localStorage.getItem('auth_token');
    headers.append('Content-Type', 'application/json');
    headers.append('Access-Token', `Bearer ${token}`);

    return this.http.delete(`${this.usersUrl}/${id}`, {headers})
           .do(res => this.userDeleted()) 
           .catch(this.handleError);
  }

  /**
   * The user was created. Add this info to our stream 
   */
  userCreated(unit: User) {
    this.userCreatedSource.next(unit); // next is a method to pass information into this subject
  }

  /**
   * The user was deleted. Add this info to our stream
   */
  userDeleted() {
    this.userDeletedSource.next();
  }


    /**
   * 
   * @private
   * @param {any} err 
   * @returns 
   * @memberOf UserService
   */
   private toUser(user): User {
     if (user.picture_attributes !== null) {
        var user_old_images = {id: '', name: ''};
        
        let get_main_url = user.picture_attributes.name.split('/');
         
        user_old_images.id = user.picture_attributes.id;
        user_old_images.name = (get_main_url[6] + '/' + get_main_url[7] + '/' + get_main_url[8] + '/' + get_main_url[9] + '/' + get_main_url[10]);
     }
     
    //  if (user.date_of_birth !== null) {
    //     var date_of_birth = {
    //       date: { 
    //           year: parseInt("0000"), 
    //           month: parseInt("00"), 
    //           day: parseInt("00") 
    //       } 
    //     }

    //     let dob = user.date_of_birth.split('-');
    //     date_of_birth = {
    //       date: { 
    //           year: parseInt(dob[0]), 
    //           month: parseInt(dob[1]), 
    //           day: parseInt(dob[2]) 
    //       } 
    //     }
    //  }

     return {
        // Creating and returing what data format we want
        id:                  user.id,
        role_id:             user.role.id,   
        role:                user.role,
        name:                user.name,
        phone:               user.phone,
        email:               user.email, 
        email_verified:      user.email_verified, 
        phone_verified:      user.phone_verified, 
        gender:              user.gender, 
        date_of_birth:       user.date_of_birth,
        created_at:          user.created_at,
        default_image:       user.default_image,
        picture_attributes:  user_old_images,
        
     }
   }

  /**
   * Handle any errors from the API
   * This is private because we want only this service to use this
   */
  private handleError(err) {
    let errMassage: string;
    if (err instanceof Response) {
      let body = err.json() || '';
   
      let error = body.error || JSON.stringify(body);
      // errMassage = `${err.status} - ${err.statusText || ''} ${error}`;
      error = JSON.parse(error).message;
      errMassage = `${error.toString()}`;
      
      // let success = JSON.parse(error).success;

      // if (error.toString() === "Token expired" && success === false) {
      //   localStorage.removeItem('auth_token');
      //   this.loggedIn = false;
      // }
      
    } else {
      errMassage = err.message ? err.message : err.toString();
    }

    return Observable.throw(errMassage);
  }
}



