import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { environment } from '../../../../environments/environment';
import * as _ from "lodash";


// For cloudinary
import { CloudinaryOptions, CloudinaryUploader } from 'ng2-cloudinary';

import { User }    from '../../../shared/models/user';
import { Unit }    from '../../../shared/models/unit';
import { City }    from '../../../shared/models/city';
import { Amenity } from '../../../shared/models/amenity';
import { Rule }    from '../../../shared/models/rule';

import { UserService } from '../../../shared/services/users/user.service';  
import { UnitService } from '../../../shared/services/units/unit.service';  
import { CityService } from '../../../shared/services/city.service';  
import { AmenityService } from '../../../shared/services/amenity.service';  
import { RuleService }    from '../../../shared/services/rule.service';

import { ActivatedRoute } from '@angular/router';

@Component({
  styleUrls: ['./unit-edit.component.scss'],
  templateUrl: './unit-edit.component.html'
})
export class UnitEditComponent implements OnInit {
  url: string = environment.cloudinary_url;
  submitted: boolean = false;

  /* standing data goes here*/
  successMessage: string = '';
  errorMessage:   string = '';

  cloudinaryImage: any;
  prepare_images_path: any;

  uploader: CloudinaryUploader = new CloudinaryUploader(
    new CloudinaryOptions({ cloudName: environment.cloudinary_cloud_name, uploadPreset: environment.cloudinary_upload_preset_flats })
  );

  public room_types = [
    { value: '', display: 'Please Select'},
    { value: 1,  display: 'Entire Place'},
    { value: 2,  display: 'Private Room'},
    { value: 3,  display: 'Shared Room'},
  ];

  public unit_classes =[
    { value: '',  display: 'Please Select' },
    { value: 'a', display: 'A' },
    { value: 'b', display: 'B' },
    { value: 'c', display: 'C' },
  ];

  public unit_types =[
    { value: '',  display: 'Please Select' },
    { value: 1, display: 'Resort' },
    { value: 2, display: 'Villa' },
    { value: 3, display: 'Apartment' },
    { value: 4, display: 'Shaleeh' },
  ];

  public available_unit_as = [
    { value: '', display: 'Please Select' },
    { value: 1,  display: 'Daily' },
    { value: 2,  display: 'Weekly' },
    { value: 3,  display: 'Monthly' },
    { value: 4,  display: 'Max 3 months' },
  ];

  public range = new Array(31);

  public unit:       Unit;
  public cities:     City[];
  public users:      User[];
  public amenities:  Amenity[];
  public rules:      Rule[];
  /* end standing data */

  form: FormGroup;
  formErrors = {
    user_id:             '',
    title_en:            '',
    title_ar:            '',
    body_en:             '',
    body_ar:             '',
    room_type:           '',
    unit_class:          '',
    unit_type:           '',
    total_area:          '',
    available_as:        '',
    number_of_guests:    '',
    number_of_rooms:     '',
    number_of_beds:      '',
    number_of_baths:     '',
    city_id:             '',
    address:             '',
    price:               '',
    service_charge:      '',
    amenity_ids:         '',
    rule_ids:            ''
  }

  validationMessages = {
    user_id:  { required: "User is required" },
    title_en: {
      required:  "Title English is required",
      minlength: "Title English must be 10 characters long",
      maxlength: "Title English should not be more than 150 characters long"       
    },
    title_ar: {
      required:  "Title Arabic is required",
      minlength: "Title Arabic must be 10 characters long",
      maxlength: "Title Arabic should not be more than 150 characters long"       
    },
    body_en:         { required:  "Body English is required" },
    body_ar:         { required:  "Body Arabic is required" },
    room_type:       { required:  "Room Type is required" },
    unit_class:      { required:  "Unit Class is required" },
    unit_type:       { required:  "Unit Type is required" },
    total_area:      { required:  "Total Area is required", pattern: "Total Area can only contain decimal value" },
    latitude:        { required:  "Latitude is required", pattern: "Latitude can only contain decimal value" },
    longitude:       { required:  "Longitude is required", pattern: "Longitude can only contain decimal value" },
    available_as:    { required:  "Unit available as is required" },
    number_of_guests:{ required:  "Number Of Guests is required" },
    number_of_rooms: { required:  "Number Of Rooms is required" },
    number_of_beds:  { required:  "Number Of Beds is required" },
    number_of_baths: { required:  "Number Of Baths is required" },
    city_id:         { required:  "City is required" },
    address:         { required:  "Address is required" },
    price:           { required:  "Price is required", pattern: "Price value must be like 000.00" },
    service_charge:  { required:  "Service Charge is required", pattern: "Service Charge must be like 0.00" },
    amenity_ids:     { required:  "Amenities are required" },
    rule_ids:        { required:  "Rules are required" }
  }

  constructor(
    private service:        UnitService, 
    private route:          ActivatedRoute, 
    private fb:             FormBuilder, 
    private cityService:    CityService, 
    private userService:    UserService,
    private amenityService: AmenityService,
    private ruleService:    RuleService,
  ) {
    //Override onSuccessItem function to record cloudinary response data
    this.uploader.onSuccessItem = (item: any, response: string, status: number, headers: any) => {
     
      //response is the cloudinary response
      //see http://cloudinary.com/documentation/upload_images#upload_response
      this.cloudinaryImage = JSON.parse(response);

      this.prepare_images_path = "v" + this.cloudinaryImage.version + "/" + this.cloudinaryImage.public_id + '.' + this.cloudinaryImage.format
      
       if (this.unit.pictures_attributes !== undefined) {
          this.unit.pictures_attributes.push({
            id: '',
            name: this.prepare_images_path
          }); 
       } else {
         this.unit.pictures_attributes = [{
           id: '',
           name: this.prepare_images_path
         }];
            
       }
      
      return {item, response, status, headers};
    };
  }

  ngOnInit(){
    
    // Build the data model for our form
    this.buildForm();
    
    // Grab the unit id from url
    let id = this.route.snapshot.params['id'];
    this.service.getUnit(id).subscribe( unit => this.unit = unit );
    
    // City List getting from CityService
    this.cityService.getCities().subscribe( cities => this.cities = cities )

    // Users who are owners get list from UserService
    this.userService.getOwners().subscribe( users => this.users = users )

    // Amenity List getting from AmenityService
    this.amenityService.getAmenities().subscribe( amenities => this.amenities = amenities )

    // Rule List getting from RuleService
    this.ruleService.getRules().subscribe( rules => this.rules = rules )
  }

  /**
   * Build Form 
   * @memberOf UnitEditComponent
   */
  buildForm() {
    // Build a Form using FormBuilder instead of FormControl
    this.form = this.fb.group({ // here i am using [''] instead of FormControl 
      user_id:             [''], 
      title_en:            ['', [Validators.minLength(10), Validators.maxLength(150)]],
      title_ar:            ['', [Validators.minLength(10), Validators.maxLength(150)]],
      body_en:             ['', [Validators.minLength(10)]],
      body_ar:             ['', [Validators.minLength(10)]],
      room_type:           [''],
      unit_class:          [''],
      unit_type:           [''],
      total_area:          ['', Validators.pattern('^[-+]?[0-9]{1,7}(\.[0-9]+)?$')],
      latitude:            ['', Validators.pattern('^[-+]?[0-9]{1,7}(\.[0-9]+)?$')],
      longitude:           ['', Validators.pattern('^[-+]?[0-9]{1,7}(\.[0-9]+)?$')],
      available_as:        [''],
      number_of_guests:    [''],
      number_of_rooms:     [''],
      number_of_beds:      [''],
      number_of_baths:     [''],
      city_id:             [''],
      address:             [''],
      price:               ['', Validators.pattern('^[-+]?[0-9]{1,7}(\.[0-9]+)?$')],
      service_charge:      ['', Validators.pattern('^[-+]?[0-9]{1,7}(\.[0-9]+)?$')],
      amenity_ids:         [''],
      rule_ids:            [''] 
    });

    // watch for changes and Validate
    this.form.valueChanges.subscribe(data => this.validateForm());
  }

  /**
   * Validate Form 
   * @memberOf UnitEditComponent
   */
  validateForm() {
     // loop over the form errors field name
     for(let field in this.formErrors){
        // clear that input field error
        this.formErrors[field] = '';

        // Grab the input by name
        let input = this.form.get(field);

        if (input.invalid && input.dirty){
          // console.log(input);
          // Figure out the type of error
          // Loop over the formErrors  fields name
          for (let error in input.errors) {
            // Assign that type of error message to a variable
            this.formErrors[field] = this.validationMessages[field][error];
          }
        }
     }
  }  

/**
 * Check form validation on submit
 */
  checkUnitEditForm(inValid) {
     if (inValid){ // Required for validation to get trigger on form submit
      this.submitted = true;
    } else { 
      // Form fields are valid now save it
      this.updateUnit();
    }
  }

/**
 * Update the Unit
 */
  updateUnit() {
    this.successMessage = '';
    this.errorMessage = '';

    // Make sure literal in array should not be empty

    this.service.updateUnit(this.unit)
      .subscribe(
        unit => {
          this.unit.pictures_attributes = unit.pictures_attributes;
          this.successMessage = "Unit was updated.";
          this.clearMessages();
           // Clear Queue
          this.uploader.clearQueue();
        },
        err => { // This is second parameter to subscribe
          // this.errorMessage = "Unit could not be updated."
          this.errorMessage = err;
          this.clearMessages();
        }
      );
  }

  /**
   * Delete Image of Image from Cloudinary and database
   */
  deleteImage(id, unit_id, name = '') {
    this.successMessage = '';
    this.errorMessage = '';

    if (confirm('Are you sure you want to delete this image?')) {
        // Delete image from cloudinary 
        if ( id === '' && name !== '' ) {
          // this.errorMessage = "This image has not been saved to yet. kindly click on update.";
          _.remove(this.unit.pictures_attributes, {name: name});
          
          // Clear Queue
          this.uploader.clearQueue();
        } else {
          // Remove the deleted image from pictures_attributes array
          // So that user can feel that image is deleted visually
          _.remove(this.unit.pictures_attributes, {id: id});
      
          // Delete image from database
          this.service.deleteImage(id, unit_id)
              .subscribe(unit => {
                this.successMessage = "Image deleted successfully";
                this.clearMessages();
              }, err => {
                this.errorMessage = err;
                this.clearMessages();
              });
        }
    }
  }


  /**
   *  Clear all messages after 5 seconds
   */
  clearMessages() {
    setTimeout(() => {
      this.successMessage = '';
      this.errorMessage = '';
    }, 3000);
    
  }

}


