import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { environment } from '../../../../environments/environment';

import { User } from '../../../shared/models/user';
import { UserService } from '../../../shared/services/users/user.service';

@Component({
  styleUrls: ['./user-single.component.scss'],
  templateUrl: './user-single.component.html'
})
export class UserSingleComponent implements OnInit {
  user: User;
  url: string = environment.cloudinary_url;

  constructor(
    private route: ActivatedRoute, // This is for the current route
    private router: Router,        // This is the tool we will use to navigate
    private service: UserService
  ) {}

  ngOnInit(){
    // Grab the id from the url
    let id = this.route.snapshot.params['id'];

    // Use the user service to getUser()
    this.service.getUser(id).subscribe( user => this.user = user );
  }

  /**
   * Delete a user
   */
  deleteUser() {
    // Confirm box here
    if(confirm('Are you sure you want to delete.')) {
      this.service.deleteUser(this.user.id)
          .subscribe(data => {
            // route back to users page
            this.router.navigate(['/dashboard/users']);
          })
    }
  }

}


