import { Injectable } from '@angular/core';
import { Http, Response, Headers } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';


import { Unit } from '../../models/unit';

import { environment } from '../../../../environments/environment';

@Injectable()
export class UnitService {
  private unitsUrl: string = environment.url + "units";

  loggedIn;

  // Observable source
  // This hold the data
  private unitCreatedSource  = new Subject<Unit>();
  private unitDeletedSource  = new Subject();  
  private unitRestoredSource = new Subject();  

  // Observable Stream
  // We are subscribing to get information from
  // As per current standard instead of userCreatedStream we will use userCreated$
  unitCreated$  = this.unitCreatedSource.asObservable();
  unitDeleted$  = this.unitDeletedSource.asObservable();
  unitRestored$ = this.unitRestoredSource.asObservable();

  constructor(private http: Http) { }

 /**
  * Get All Units
  */
  //  getUnits(): Observable<Unit[]> {
  //   let headers = new Headers();
  //   let token = localStorage.getItem('auth_token');
  //   headers.append('Content-Type', 'application/json');
  //   headers.append('Access-Token', `Bearer ${token}`);

  //   // return this.http.get(this.unitsUrl, { headers })
  //   //   .map( res => res.json().data.units )
  //   //   .map( units => units.map(this.toUnit) )
  //   //   .catch( this.handleError );
  // }


  getUnits(page: number): Observable<Unit[]> {
    let headers = new Headers();
    let token = localStorage.getItem('auth_token');
    headers.append('Content-Type', 'application/json');
    headers.append('Access-Token', `Bearer ${token}`);
    
    const offset = (page - 1) * 10;
    return this.http.get(this.unitsUrl + '?limit=10' + '&offset=' + offset, { headers })
      .map( res => res.json() )
      .catch( this.handleError );
  }

 /**
  * Get A Single Unit
  */
  getUnit(id: number): Observable<Unit> {
    let headers = new Headers();
    let token = localStorage.getItem('auth_token');
    headers.append('Content-Type', 'application/json');
    headers.append('Access-Token', `Bearer ${token}`);

    return this.http.get(`${this.unitsUrl}/${id}`, { headers })
      .map(res => res.json().data.units)
      .map(this.toUnit)
      .catch(this.handleError);
 }

 /**
  * Create A Unit
  */  
 createUnit(unit: Unit): Observable<Unit> {
    // Attaching a token
    let headers = new Headers();
    let token = localStorage.getItem('auth_token');
    headers.append('Content-Type', 'application/json');
    headers.append('Access-Token', `Bearer ${token}`);

    return this.http.post(this.unitsUrl, unit, {headers})
      .map(res => res.json().data === null ? res.json().message : res.json().data.units)
      .do(unit => this.unitCreated(unit))
      .catch(this.handleError)
 }

 /**
  * Update A Unit
  */ 
 updateUnit(unit: Unit): Observable<Unit> {
    // Attaching a token
    let headers = new Headers();
    let token = localStorage.getItem('auth_token');
    headers.append('Content-Type', 'application/json');
    headers.append('Access-Token', `Bearer ${token}`);

    // put has url and data or information as second parameter
    return this.http.put(`${this.unitsUrl}/${unit.id}`, unit, {headers}) 
      .map(res => res.json().data.units)
      .map(this.toUnit)
      .catch(this.handleError);
 }

  /**
   * Delete A Unit
   */
  deleteUnit(id: number): Observable<any>{ // If we want this to return something that use Observable<any>
    // Attaching a token
    let headers = new Headers();
    let token = localStorage.getItem('auth_token');
    headers.append('Content-Type', 'application/json');
    headers.append('Access-Token', `Bearer ${token}`);

    return this.http.delete(`${this.unitsUrl}/${id}`, { headers })
           .do(res => this.unitDeleted()) 
           .catch(this.handleError);
  }

  /**
   * Delete Unit Image 
   */
  deleteImage(id: number, unit_id: number): Observable<any> {
     // Attaching a token
    let headers = new Headers();
    let token = localStorage.getItem('auth_token');
    headers.append('Content-Type', 'application/json');
    headers.append('Access-Token', `Bearer ${token}`);
    console.log("Here:" + id);
    return this.http.delete(`${environment.url}pictures/${id}`, { body: {unit_id: unit_id}, headers })
               .map(res => res.json().data.units)
               .catch(this.handleError);
  }

  /**
   * Restore A Unit
   */
  restoreUnit(id: number): Observable<any>{ // If we want this to return something that use Observable<any>
    // Attaching a token
    let headers = new Headers();
    let token = localStorage.getItem('auth_token');
    headers.append('Content-Type', 'application/json');
    headers.append('Access-Token', `Bearer ${token}`);

    return this.http.put(`${this.unitsUrl}/${id}/restore`, null, { headers })
           .do(res => this.unitRestored())  
           .catch(this.handleError);
  }

  /**
   * The unit was created. Add this info to our stream 
   */
  unitCreated(unit: Unit) {
    this.unitCreatedSource.next(unit); // next is a method to pass information into this subject
  }

  /**
   * The unit was deleted. Add this info to our stream
   */
  unitDeleted() {
    this.unitDeletedSource.next();
  }

  /**
   * The unit was restored. Add this info to our stream
   */
  unitRestored() {
    this.unitRestoredSource.next();
  }

  /**
   * 
   * @private
   * @param {any} err 
   * @returns 
   * @memberOf UnitService
   */
   private toUnit(unit): Unit {
      let amenity_list = [];
      for (var i = 0; i < unit.amenities.length; i++) {
        amenity_list.push(unit.amenities[i].id);
      }

      let rule_list = [];
      for (var i = 0; i < unit.rules.length; i++) {
        rule_list.push(unit.rules[i].id);
      }

      if (unit.images.length > 0) {
        var old_images = [];
        for (var i = 0; i < unit.images.length; i++) {
          let get_main_url = unit.images[i].url.split('/');
         
          old_images.push({
            id: unit.images[i].id,
            name: (get_main_url[6] + '/' + get_main_url[7] + '/' + get_main_url[8] + '/' + get_main_url[9] + '/' + get_main_url[10])
        })
        }
      }
     
     return {
        // Creating and returing what data format we want
        id:                  unit.id,
        user_id:             unit.owner.user_id,
        title_en:            unit.title_en,
        title_ar:            unit.title_ar,
        body_en:             unit.body_en,
        body_ar:             unit.body_ar,
        room_type:           unit.room_type,
        number_of_guests:    unit.number_of_guests,
        number_of_rooms:     unit.number_of_rooms,
        number_of_beds:      unit.number_of_beds,
        number_of_baths:     unit.number_of_baths,
        available_as:        unit.available_as,
        city_id:             unit.city.id,
        city_name:           unit.city.name,
        address:             unit.address,
        price:               unit.price,
        service_charge:      unit.service_charge,
        soft_delete:         unit.soft_delete,
        latitude:            unit.latitude,
        longitude:           unit.longitude,
        unit_class:          unit.unit_class,
        unit_type:           unit.unit_type,
        total_area:          unit.total_area,
        created_at:          unit.created_at,
        owner:               unit.owner,
        pictures_attributes: unit.images === null ? unit.default_name : old_images,
        amenity_ids:         amenity_list,
        rule_ids:            rule_list
     }
   }

   /**
    * 
    */
  // private toUnitImage(unit): Unit {
  //   return {
  //      pictures_attributes: 
  //   }
  // }  

  /**
   * Handle any errors from the API
   * This is private because we want only this service to use this
   */
  private handleError(err) {
    let errMassage: string;
    if (err instanceof Response) {
      let body = err.json() || '';
      let error = body.error || JSON.stringify(body);
    
      let msg = JSON.parse(error).message;
      let success = JSON.parse(error).success;

      if (msg === "Token expired" && success === false) {
        localStorage.removeItem('auth_token');
        this.loggedIn = false;
      }

      errMassage = `${err.status} - ${err.statusText} || ''} ${error}`;
    } else {
      errMassage = err.message ? err.message : err.toString();
    }

    return Observable.throw(errMassage);
  }


 
}



