import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';

import { Observable } from 'rxjs/Observable';

import { environment } from '../../../environments/environment';

@Injectable()
export class AuthService {

  private authUrl: string = environment.url + 'login';
  private loggedIn: boolean = false;
  
  constructor(private http: Http){
    // Look into Localstorage to check if user is  logged into
    this.loggedIn = !!localStorage.getItem('auth_token'); // !! is a quick way to typecast a boolean
  }

  /**
   * Check if the user is loggedIn
   */
  isLoggedIn() {
    return this.loggedIn;
  }
  
  /**
   * Log the user out
   */
  logout() {
    localStorage.removeItem('auth_token');
    this.loggedIn = false;
  }

  /**
   *  Log the user in
   */
  login(email: string, password: string): Observable<string> {
    return this.http.post(`${this.authUrl}`, { email, password })
      .map(res => res.json().data)
      .do(res => {
        if (res.auth_token) {
          localStorage.setItem('auth_token', res.auth_token);
          this.loggedIn = true;
        }
      })
      .catch(this.handleError);
  }

   /**
   * Handle any errors from the API
   * This is private because we want only this service to use this
   */
  private handleError(err) {
    let errMassage: string;
    if (err instanceof Response) {
      let body = err.json() || '';
      let error = body.error || JSON.stringify(body);
      // errMassage = `${err.status} - ${err.statusText || ''} ${error}`;
      // error = JSON.parse(error).message;
      // errMassage = `${error}`;
      error = JSON.parse(error).message;
      errMassage = `${error}`;
    } else {
      errMassage = err.message ? err.message : err.toString();
    }

    return Observable.throw(errMassage);
  } 
}


