import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { Ng2CloudinaryModule } from 'ng2-cloudinary';
import { FileUploadModule } from 'ng2-file-upload';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { Ng2PaginationModule } from 'ng2-pagination';
import { TruncateModule } from 'ng2-truncate';
import { DatePickerModule } from 'ng2-datepicker';

// App Components
import { DashboardComponent } from './dashboard.component';
import { NavbarComponent } from './navbar/navbar.component';
import { SidebarComponent } from './sidebar/sidebar.component';
import { FooterComponent } from './footer/footer.component';

import { DashyComponent } from './dashy/dashy.component';

// Dashboard User Section Components
import { UsersComponent } from './users/users.component'
import { UserListComponent } from './users/user-list/user-list.component';
import { UserCreateComponent } from './users/user-create/user-create.component';
import { UserEditComponent } from './users/user-edit/user-edit.component';
import { UserSingleComponent } from './users/user-single/user-single.component';

import { UserService } from '../shared/services/users/user.service';
import { UnitService } from '../shared/services/units/unit.service';
import { CityService } from '../shared/services/city.service';
import { AmenityService } from '../shared/services/amenity.service';
import { RoleService } from '../shared/services/role.service';
import { AuthService } from '../shared/services/auth.service';
import { AuthGuard } from '../shared/guards/auth-guard.service';
import { CanDeactivateGuard } from '../shared/guards/can-deactivate-guard.service';
import { RuleService } from '../shared/services/rule.service';

import { dashboardRouting } from './dashboard.router';

// Dashboard Units Section Components
import { UnitsComponent } from './units/units.component';
import { UnitListComponent } from './units/unit-list/unit-list.component';
import { UnitSingleComponent } from './units/unit-single/unit-single.component';
import { UnitCreateComponent } from './units/unit-create/unit-create.component';
import { UnitEditComponent } from './units/unit-edit/unit-edit.component';


@NgModule({
  declarations: [
    DashboardComponent,
    NavbarComponent,
    SidebarComponent,
    FooterComponent,
    DashyComponent,
    UsersComponent,
    UserListComponent,
    UserCreateComponent,
    UserEditComponent,
    UserSingleComponent,
    UnitsComponent,
    UnitListComponent,
    UnitSingleComponent,
    UnitCreateComponent,
    UnitEditComponent
  ],
  imports: [
    CommonModule,
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    DatePickerModule,
    dashboardRouting,
    Ng2CloudinaryModule,
    FileUploadModule,
    TruncateModule,
    Ng2PaginationModule
  ],
  providers: [
    AuthService,
    AuthGuard,
    UserService,
    UnitService,
    CityService,
    AmenityService,
    RoleService,
    CanDeactivateGuard,
    RuleService
  ]
})
export class DashboardModule {}


