import { Component, OnInit } from '@angular/core';

import { Router } from '@angular/router';
import { UnitService } from '../../../shared/services/units/unit.service';  
import { Unit } from '../../../shared/models/unit';

@Component({
  templateUrl: './unit-list.component.html',
  styleUrls: ['./unit-list.component.scss']
})
export class UnitListComponent implements OnInit {
  units: Unit[];
  event: number = 1; 
  errorMessage: string = '';
  successMessage: string = '';
  totalItem: number;
  
  constructor(
    private service: UnitService,
    private router: Router        // This is the tool we will use to navigate
  ) { 
    this.getServerData(1); 
  }

  ngOnInit(){
    //  this.service.getUnits().subscribe( units => this.units = units );
  }

  getServerData(event){
     this.service.getUnits(event).subscribe( res => { 
        this.units = res["data"].units;
        this.totalItem = res["data"].total; 
       }
     );
    this.event = event; 
		return event;
	}


 /**
  * Delete a unit
  */
  deleteUnit(unit_id) {
    // Confirm box here
    if(confirm('Are you sure you want to delete.')) {
      this.service.deleteUnit(unit_id)
          .subscribe(data => {
            this.successMessage = "Unit deleted successfully.";
            this.getServerData(this.event); // Refresh table data once data updated
            this.router.navigate(['/dashboard/units']);   // route back to users page
          })
    }
  }

  /**
  * Restore a unit
  */
  restoreUnit(unit_id) {
    this.service.restoreUnit(unit_id)
        .subscribe(data => {
          this.successMessage = "Unit restored successfully.";
          this.getServerData(this.event); // Refresh table data once data updated
          this.router.navigate(['/dashboard/units']); // route back to users page
        })
  }

 
}



