import { Component, OnInit } from '@angular/core';

import { UserService } from '../../shared/services/users/user.service';

@Component({
  templateUrl: './users.component.html'
})
export class UsersComponent implements OnInit {

  successMessage: string = '';
  errorMessage: string = '';

  constructor(private service: UserService) {}

  ngOnInit() {
    // Show Message of unit has been created
    this.service.userCreated$.subscribe(unit => {
      // this.successMessage = `${unit.name} has been created`;
      this.successMessage = 'User has been created';
      this.clearMessages();
    });

    // Show Message of unit has been deleted 
    this.service.userDeleted$.subscribe(() => { // As this is not return any user so we would use only () blank
      this.successMessage = 'The user has been deleted';
      this.clearMessages();
    });
  }

  /**
   *  Clear all messages after 5 seconds
   */
  clearMessages() {
    setTimeout(() => {
      this.successMessage = '';
      this.errorMessage = '';
    }, 5000);
    
  }
}


