import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { User } from '../../../shared/models/user';
import { UserService } from '../../../shared/services/users/user.service';

@Component({
  styleUrls: ['./user-list.component.scss'],
  templateUrl: './user-list.component.html'
})
export class UserListComponent implements OnInit {
  
  users: User[];

  event: number = 1; 
  errorMessage: string = '';
  successMessage: string = '';
  totalItem: number;

  constructor(
    private service: UserService,
    private router: Router
  ) {
    this.getUsers(1); 
  }

  ngOnInit(){
    // this.service.getUsers().subscribe( users => this.users = users )
  }

  getUsers(event){
     this.service.getUsers(event).subscribe( res => { 
        this.users = res["data"].users;
        this.totalItem = res["data"].total; 
       }
     );
    this.event = event; 
		return event;
	}

 /**
  * Delete a user
  */
  deleteUser(user_id) {
    // Confirm box here
    if(confirm('Are you sure you want to delete?')) {
      this.service.deleteUser(user_id)
          .subscribe(data => {
            this.getUsers(this.event); // Refresh table data once data updated
            this.router.navigate(['/dashboard/users']);   // route back to users page
          })
    }     
  }
}


