import { Component, OnInit  } from '@angular/core';

import { UnitService } from '../../shared/services/units/unit.service';  

@Component({
  templateUrl: './units.component.html'
})
export class UnitsComponent implements OnInit {

  successMessage: string = '';
  errorMessage: string = '';

  constructor(private service: UnitService) {}

  ngOnInit() {
    // Show Message of unit has been created
    this.service.unitCreated$.subscribe(unit => {
      // this.successMessage = `${unit.name} has been created`;
      this.successMessage = 'Unit has been created';
      this.clearMessages();
    });

    // Show Message of unit has been deleted 
    this.service.unitDeleted$.subscribe(() => { // As this is not return any user so we would use only () blank
      this.successMessage = 'The unit has been deleted';
      this.clearMessages();
    });

    // Show Message unit has been restored 
    this.service.unitRestored$.subscribe(() => { // As this is not return any user so we would use only () blank
      this.successMessage = 'The unit has been restored';
      this.clearMessages();
    });  
  }

  /**
   *  Clear all messages after 5 seconds
   */
  clearMessages() {
    setTimeout(() => {
      this.successMessage = '';
      this.errorMessage = '';
    }, 6000);
    
  }
}


