import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { environment } from '../../../../environments/environment';

import { Unit } from '../../../shared/models/unit';
import { UnitService } from '../../../shared/services/units/unit.service';

@Component({
  templateUrl: './unit-single.component.html'
})
export class UnitSingleComponent implements OnInit {
  unit: Unit;
  url: string = environment.cloudinary_url;
  
  constructor(
    private route: ActivatedRoute, // This is for the current route
    private router: Router,        // This is the tool we will use to navigate
    private service: UnitService
  ) {}

  ngOnInit() {
    // Grab the id from the url
    let id = this.route.snapshot.params['id'];

    // Use the user service to getUser()
    this.service.getUnit(id)
      .subscribe(unit => this.unit = unit);
  }
}


