import { Component, OnInit } from '@angular/core';

import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DatePickerOptions, DateModel } from 'ng2-datepicker';

import { Router, ActivatedRoute } from '@angular/router';
import { environment } from '../../../../environments/environment';

// For cloudinary
import { CloudinaryOptions, CloudinaryUploader } from 'ng2-cloudinary';

// Importing Models
import { User } from '../../../shared/models/user';
import { Role } from '../../../shared/models/role';

// Importing Services
import { UserService } from '../../../shared/services/users/user.service';
import { RoleService } from '../../../shared/services/role.service';

@Component({
  templateUrl: './user-edit.component.html',
  styleUrls: ['./user-edit.component.scss']
})
export class UserEditComponent implements OnInit {
  user: User;
  submitted: boolean = false;
  url: string = environment.cloudinary_url;
 
  date: DateModel;
  show: boolean;
 
  successMessage: string = '';
  errorMessage: string = '';
  
  options: DatePickerOptions; 
  genders = ['Male', 'Female'];

  cloudinaryImage: any;
  prepare_images_path: any;

  uploader: CloudinaryUploader = new CloudinaryUploader(
    new CloudinaryOptions({ cloudName: environment.cloudinary_cloud_name, uploadPreset: environment.cloudinary_upload_preset_users })
  );

  form: FormGroup;
  formErrors = {
    role_id:        '',
    name:           '',
    email:          '',
    phone:          '',
    gender:         '',
    email_verified: '',
    phone_verified: ''
  }

  validationMessages = {
    role_id:       { required:  "Role is required" },
    name:          { 
      required:  "Name is required",
      minlength: "Name must be minimum of 3 character long",
      maxlength: "Name should not be more than 100 character long"
    },
    email:         { 
      required:  "Email is required",
      minlength: "Email must be minimum of 7 characters long",
      maxlength: "Email should not be more than 100 characters long",
      pattern:   "Email is not valid"
    },
    phone:         { 
      required:  "Phone is required",
      minlength: "Phone must be minimum of 10 digits long",
      maxlength: "Phone should not be more than 10 digits long",
      pattern:   "Phone can only contain numeric values"
    },
    gender:        { required:  "Gender is required" }
  }
  
  public roles:  Role[];
  
  constructor(
    private service: UserService, 
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private roleService: RoleService
  ) {
      this.options = new DatePickerOptions();

    //Override onSuccessItem function to record cloudinary response data
    this.uploader.onSuccessItem = (item: any, response: string, status: number, headers: any) => {
      //response is the cloudinary response
      //see http://cloudinary.com/documentation/upload_images#upload_response
      this.cloudinaryImage = JSON.parse(response);

      this.prepare_images_path = "v" + this.cloudinaryImage.version + "/" + this.cloudinaryImage.public_id + '.' + this.cloudinaryImage.format
      
      if (this.user.picture_attributes !== undefined) {
        this.user.picture_attributes.id = '';
        this.user.picture_attributes.name = this.prepare_images_path;
      } else {
        this.user.picture_attributes = { id: '', name: this.prepare_images_path };
      }
      
      return {item, response, status, headers};
    };
  }
  
  ngOnInit() {
    // Grab the user id from url
    let id = this.route.snapshot.params['id'];
    this.service.getUser(id).subscribe(user => {
      this.user = user;
      this.show = ((this.user.picture_attributes === undefined) || (this.user.picture_attributes === null))? false : true ; 
    });

    // Build the data model for our form
    this.buildForm();

    this.roleService.getRoles().subscribe( roles => this.roles = roles)
  }

  /**
   * Build Form 
   * @memberOf UnitEditComponent
   */
  buildForm() {
    let emailRegex = '^[0-9]+$';
  
    // Build a Form using FormBuilder instead of FormControl
    this.form = this.fb.group({ // here i am using [''] instead of FormControl 
      role_id:        [''], 
      name:           ['', [Validators.minLength(3), Validators.maxLength(100)]],
      email:          ['', [Validators.pattern("[a-zA-Z0-9._-]+@([a-zA-Z0-9-]){2,}\.[a-zA-Z]{2,6}"),Validators.minLength(7), Validators.maxLength(100)]],
      phone:          ['', [Validators.pattern('[0-9]+'),Validators.minLength(10), Validators.maxLength(10)]],
      date_of_birth:  [''],
      gender:         [''],
      email_verified: '',
      phone_verified: ''
    });

    // watch for changes and Validate
    this.form.valueChanges.subscribe(data => this.validateForm());
  }

  /**
   * Validate Form 
   * @memberOf UnitEditComponent
   */
  validateForm() {
     // loop over the form errors field name
     for(let field in this.formErrors){
        // clear that input field error
        this.formErrors[field] = '';

        // Grab the input by name
        let input = this.form.get(field);

        if (input.invalid && input.dirty){
          // console.log(input);
          // Figure out the type of error
          // Loop over the formErrors  fields name
          for (let error in input.errors) {
            // Assign that type of error message to a variable
            this.formErrors[field] = this.validationMessages[field][error];
          }
        }
     }
  }

   /**
   * Check form validation on submit
   */
  checkUserEditForm(inValid) {
     if (inValid){ // Required for validation to get trigger on form submit
      this.submitted = true;
    } else { 
      // Form fields are valid now save it
      this.updateUser();
    }
  }

  /**
   * Update the User
   */
  updateUser() {
    this.successMessage = '';
    this.errorMessage = '';

    if (this.user.date_of_birth) {
      this.user.date_of_birth = this.user.date_of_birth["formatted"];
    }

    this.service.updateUser(this.user).subscribe( user => {
          this.successMessage = "User was updated.";
          this.clearMessages();
          // Clear Queue
          this.uploader.clearQueue();
        },
        err => { // This is second parameter to subscribe
          this.errorMessage = err;
          this.clearMessages();
        }
      );
  }


  // canDeactivate() {
  //   console.log('i am navigating away');

  //   // if the this.route.snapshot.params['id'] !== this.user.id
  //   if (this.route.snapshot.params['id'] !== this.user.id) {
  //     return window.confirm('Discard Changes?');
  //   }

  //   return true;
  // }


  /**
   *  Clear all messages after 5 seconds
   */
  clearMessages() {
    setTimeout(() => {
      this.successMessage = '';
      this.errorMessage = '';
    }, 3000);
    
  }

}


